@import url(https://fonts.googleapis.com/css2?family=Saira+Stencil+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Diplomata&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer {
  align-items: center;
  background-color: rgb(255, 255, 77);
  bottom: 0;
  display: flex;
  justify-content: center;
  left: 0;
  position: fixed;
  width: 100vw;
}

.footer-content {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 15px 30px;
  width: 100%;
}

.search-body {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 20px;
}

.search-body input {
  margin: 5px 0;
  padding: 1px 10px;
  width: 100%;
}

.search-body label {
  padding: 0 15px;
}

.search-body button {
  background-color: red;
  border-radius: 5px;
  color: white;
  font-size: 15px;
  font-weight: 600;
  margin: auto;
  margin-top: 15px;
  padding: 5px 10px;
  width: 50%;
}

.comidas-body .header, .bebidas-body .header, .explore .header {
  background-color: rgb(255, 255, 102);
  display: flex;
  flex-direction: column;
}

.comidas-body .header-content, .bebidas-body .header-content, .explore .header-content {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 5px 20px;
}

.explore-big .header {
  background-color: rgb(255, 255, 102);
  display: flex;
  flex-direction: column;
}

.explore-big .header-content {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 25px 0 25px 10px;
}

.explore-big .header h1 {
  background: none;
  color: rgb(44, 44, 44);
  display: flex;
  flex-direction: column;
  font-size: 25px;
  font-weight: 900;
  margin: 0 10px 0 0;
  text-align: center;
}

.done-recipes-body .header {
  background-color: rgb(236, 83, 83);
  display: flex;
  flex-direction: column;
}

.done-recipes-body .header-content {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 15px 0 15px 10px;
}

.done-recipes-body .header h1 {
  background: none;
  color: rgb(44, 44, 44);
  display: flex;
  flex-direction: column;
  font-size: 33px;
  font-weight: 900;
  margin: 0 10px 0 0;
  text-align: end;
}

.food-image {
  border-radius: 6px;
  height: 200px;
  max-width: 300px;
  width: 100%;
}

.comidas-body .card, .bebidas-body .card {
  animation: glass1 1s linear infinite;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  background-color: rgba(202, 202, 202, 0.452);
  border-radius: 6px;
  box-shadow: 0 0 15px rgb(177, 177, 177);
  margin: 20px;
  padding: 20px;
}

.card .food-title {
  border-radius: 10px;
  color: rgb(36, 36, 36);
  font-family: Saira Stencil One , cursive;
  font-weight: 900;
  text-align: center;
  text-decoration: none;
  text-shadow: 5px 5px 5px white;
  text-transform: uppercase;
}

@keyframes glass1 {

  0% {
    background: rgba(168, 167, 167, 0.6);
  }

  100% {
    background: rgba(167, 167, 167, 0.6);
  }
}

.categorias {
  background-color: rgb(255, 255, 110);
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding: 0 20px;
  padding-bottom: 20px;
}

.comidas-body .categorias button {
  background-color: white;
  border: none;
  font-size: 14px;
  margin-top: 5px;
  padding: 5px 10px;
  width: 30%;
}

.bebidas-body .categorias button {
  background-color: white;
  border: none;
  font-size: 13px;
  margin-top: 5px;
  padding: 5px 0;
  width: 32%;
}

.details {
  padding: 30px 20px 50px;
}

.details p {
  padding: 10px;
  text-align: justify;
}

.details img {
  height: 200px;
  margin: auto;
  width: 100%;
}

.details .start-recipe-btn {
  background-color: green;
  bottom: 0;
  color: white;
  font-size: 20px;
  font-weight: 600;
  left: 0;
  padding: 10px;
  position: fixed;
  width: 100%;
  z-index: 999;
}

.details .icons {
  position: absolute;
  right: 5px;
  text-align: end;
  top: 5px;
  width: 100px;
}

.details .favorite-btn {
  margin-left: 10px;
}

.details .copied-link {
  background-color: white;
  font-size: 16px;
  position: absolute;
  right: -5px;
  text-align: center;
  top: -5px;
  width: 80px;
}

.ingredient-list li {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.ingredient-list li label {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding-right: 30px;
  width: 100%;
}

.ingredient-list {
  padding: 20px;
}

.selected-li {
  background-color: rgb(221, 221, 221);
  border-radius: 6px;
  text-decoration: line-through;
}

.loading-body {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  position: absolute;
  width: 100vw;
}


.loading {
  animation: loading 1s linear infinite;
  border-left: 2px solid black;
  border-radius: 50%;
  height: 50px;
  width: 50px;
}

@keyframes loading {

  to {
    transform: rotate(360deg);
  }

}

.comidas-body .loading {
  margin: auto;
  margin-top: 150px;
}

.recommended-image {
  border-radius: 6px;
  height: 200px;
  max-width: 300px;
  width: 100%;
}

.recommended-card {
  background-color: rgba(216, 216, 216, 0.5);
  border-radius: 6px;
  box-shadow: 1px 1px 5px rgb(141, 141, 141);
  color: black;
  font-weight: 600;
  margin: 20px;
  padding: 10px 30px 20px;
  text-align: center;
}

