.explorar-body {
  height: 79vh;
}

.explorar-body .options {
  align-items: center;
  display: flex;
  height: 50%;
  justify-content: center;
}

.explorar-body .options.food {
  background-image: url(/App-Receitas/static/media/comidas.69200fc7.jpg);
  background-size: cover;
  border-bottom: 2px white solid;
}

.explorar-body .options.drink {
  background-image: url(/App-Receitas/static/media/bebidas.d3639786.jpeg);
  background-size: cover;
  border-top: 2px white solid;
}

.explorar-body .options .button {
  background: rgba(2, 2, 2, 0.795);
  border: white 4px solid;
  color: white;
  font-size: 30px;
  font-weight: 700;
  padding: 5px 20px;
  text-align: center;
}

.explore-big .btn-content {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 79vh;
  justify-content: center;
}

.explore-big .btn-content a, .explore-big .btn-content button {
  background: rgb(255, 255, 99);
  border: 1px black solid;
  color: black;
  font-size: 24px;
  margin: 10px;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  text-shadow: black 2px 3px 10px;
  width: 90%;
}

.per-ingredients .list {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 20px 20px 80px;
  width: 100%;
}

.per-ingredients .list label {
  align-items: center;
  background: rgb(201, 201, 201);
  border-radius: 10px;
  display: flex;
  height: 200px;
  justify-content: center;
  padding: 0 5px;
  text-align: center;
  width: 45%;
}

.per-ingredients .list label input {
  display: none;
}

.explore-area {
  background: url(data:image/png;base64,) no-repeat right rgb(231, 231, 231);
  background-position: 95%;
  background-size: 30px;
  border: 1px solid #ccc;
  height: 34px;
  margin: auto;
  margin-top: 10px;
  overflow: hidden;
  width: 90%;
}

.explore-area .dropdown-areas {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: transparent;
  border: 0;
  border-radius: 0;
  font-size: 16px;
  height: 34px;
  line-height: 1;
  padding: 5px;
  width: 100%;
}

.login-body {
  align-items: center;
  background-image: url(/App-Receitas/static/media/login-wallpaper.05999137.jpg);
  background-size: cover;
  display: flex;
  height: 100vh;
  justify-content: center;
  width: 100vw;
}

.login-content {
  background-color: rgba(0, 0, 0, 0.616);
  box-shadow: 1px 1px 10px;
  display: flex;
  flex-direction: column;
  max-width: 90%;
  padding: 20px;
}

.login-content .title {
  color: white;
  text-align: center;
}

.login-content .title h1 {
  font-family: Diplomata , cursive;
  margin-bottom: 30px;
  position: relative;
}

.login-content .slogan {
  color: rgb(151, 151, 151);
  font-size: 13px;
}

.login-content .slogan p {
  margin: 0;
  padding: 0 15%;
}

.login-content .slogan .p1 {
  text-align: start;}

.login-content .slogan .p2 {
  text-align: end;}

.login-content .title h1 .by {
  color: rgb(80, 170, 72);
}

.login-content .title h1 .yourself {
  bottom: -40%;
  font-size: 20px;
  left: 35%;
  position: absolute;
}


.login-content input {
  background: none;
  border: none;
  border-bottom: white 2px solid;
  border-radius: 15px;
  color: white;
  margin: 10px;
  padding: 5px 30px;
}

.login-content input:-webkit-input-placeholder {
  color: white;
  text-align: center;
}

.login-content button {
  background-color: rgb(38, 90, 38);
  border-radius: 6px;
  color: white;
  font-size: 20px;
  font-weight: 600;
  margin: 10px;
  padding: 5px 10px;
}

.login-content button:disabled {
  background: red;
}

.login-content button:hover {
  -webkit-filter: brightness(1.5);
          filter: brightness(1.5);
}

.perfil .email {
  background: rgb(255, 255, 100);
  border-top: white 2px solid;
  font-size: 20px;
  padding: 10px 0;
  text-align: center;
  width: 100%;
}

.perfil .btn-perfil {
  background: rgb(255, 255, 99);
  border: 1px black solid;
  color: black;
  font-size: 24px;
  margin: 10px auto;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  text-shadow: black 2px 3px 10px;
  width: 90%;
}

.perfil .btn-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 70vh;
  justify-content: center;
}

.perfil a {
  text-align: center;
  width: 100%;
}

.perfil .header-content a {
  width: 10%;

}

.button-finish {
  background-color: red;
  bottom: 0;
  color: white;
  font-weight: 600;
  left: 0;
  padding: 10px 0;
  position: fixed;
  width: 100%;
}

.button-finish:disabled {
  background-color: rgb(121, 121, 121);
}

.done-card-body {
  align-items: center;
  animation: glass 0.5s linear infinite;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background: rgba(231, 231, 231);
  border-radius: 10px;
  box-shadow: 2px 3px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px;
  padding: 30px 30px 30px 10px;
  position: relative;
}

.done-card-body .card-content {
  align-items: space;
  display: flex;
  justify-content: space-between;
  text-align: end;
}

.done-card-body .card-content section {
  align-items: flex-end;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
}

.done-card-body:nth-child( odd ) .card-content section {
  align-items: flex-start;
}

.done-card-body a {
  margin: 10px;
  width: 40%;
}

.done-card-body .card-content section a {
  margin: 0;
  width: 100%;
}

.done-card-body:nth-child( odd ) .card-content {
  display: flex;
  flex-direction: row-reverse;
  text-align: start;
}

.done-card-body img {
  border-radius: 6px;
  width: 100%;
}

.done-card-body p {
  font-size: 15px;
  margin-bottom: 8px;
}

.done-card-body h1 {
  color: black;
  font-size: 20px;
  font-weight: 600;
  z-index: 1000;
}


.done-card-body h3 {
  font-size: 15px;
}

.done-card-body .tags {
  background-color: rgb(48, 40, 156);
  border-radius: 6px;
  color: white;
  font-size: 10px;
  font-weight: 600;
  margin: 0;
  padding: 2px 10px;
  text-align: center;
  width: 48%;
}


.done-card-body .share-btn {
  position: absolute;
  right: 10px;
  top: 10px;
}

.done-card-body .finish-date {
  bottom: 0;
  position: absolute;
  right: 20px;
}

.done-card-body:nth-child( odd ) .finish-date {
  left: 20px;
}

.tag-container {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}

@keyframes glass {

  0% {
    background: rgba(180, 180, 180, 0.6);
  }

  100% {
    background: rgba(180, 180, 180, 0.6);
  }
}

.done-recipes-body .title {
  font-weight: 700;
  padding-top: 20px;
  text-align: center;
}

.done-recipes-body .head {
  background: rgb(236, 83, 83);
  color: white;
  padding: 10px;
  text-shadow: black 1px 1px 5px;
}

.done-button-content {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  width: 100%;
}

.done-button-content button {
  background: white;
  border: 2px rgb(236, 83, 83) solid;
  border-radius: 6px;
  color: rgb(236, 83, 83);
  font-weight: 600;
  width: 30%;
}

.done-button-content .selected-btn {
  background: rgb(236, 83, 83);
  border: white 2px solid;
  color: white;
}

.background-color {
  background: rgb(236, 83, 83);
  height: 100%;
  position: absolute;
  right: 0;
  width: 20%;
  z-index: -999;
}

.not-done-recipes {
  color: rgb(119, 119, 119);
  margin: 30px 50px 0 0;
  text-align: center;
}

.not-found-body {
  align-items: center;
  background: url(/App-Receitas/static/media/notfound.0901ed89.gif);
  display: flex;
  height: 100vh;
  justify-content: center;
  padding-bottom: 30px;
  width: 100vw;
}

.message {
  align-items: center;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.message h1 {
  animation: blur 3s linear infinite;
  font-size: 150px;
  font-weight: 700;
  opacity: 0.4;
}

.message .not-found {
  animation: blur 4s linear infinite;
  font-size: 50px;
  font-weight: 700;
  margin-bottom: 50px;
  opacity: 1;
}

.message button {
  background: #0a66c2;
  border-radius: 6px;
  color: white;
  font-size: 20px;
  font-weight: 600;
  padding: 5px 40px;
}

@keyframes blur {

  0% {
    -webkit-filter: blur(4px);
            filter: blur(4px);
  }

  50% {
    -webkit-filter: blur(10px);
            filter: blur(10px);
    opacity: 0.7;
  }

  100% {
    -webkit-filter: blur(4px);
            filter: blur(4px);
  }
}

.recipes-background-color {
  background-color: rgb(255, 255, 110);
  height: 100%;
  position: absolute;
  right: 0;
  width: 20%;
  z-index: -1;
}

